.columns {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  /* margin-top: 20px; */
}

.column {
  background-color: #18181a;
  /* flex: 1; */
  flex-direction: column;
  width: '100%';
  justify-content: top;
  padding-left: 5px;
  margin-right: 1%;
  margin-left: 1%;
  margin-top: 1%;
  font-size: 16px;
  /* height: '100%'; */
  /* background-color: #ccc; */
  /* display: flex; */
  /* align-items: center; */
  /* border-radius: 8px; */
}
@media screen and (min-width: 400px) {
  .column {
    width: 30%;
    margin-right: 2.5px; 
    margin-left: 2.5px; 
  }
}
.column p {
  font-size: 14px;
  color: #ffffff;
  overflow: hidden; 
  text-overflow: ellipsis; 
  white-space: normal; 
}
.column h1 {
  font-size: calc(10px + 2vmin);
  font-weight: 100;
  font-family: PlayfairDisplay;
  color: #ffffff;
  overflow: hidden; 
  text-overflow: ellipsis; 
  text-align: left;
  white-space: normal; 
}
